import { getHomeInfo } from "@/lib/get_home_info";

export const Hero = async () => {
  const {encabezado_1, encabezado_2, encabezado_3, descripcion, tituloMultimedia, video} = await getHomeInfo();	

  return (
    <section className="pt-10 max-w-[1440px] mx-auto relative flex flex-col">
      <div className="flex flex-col gap-10 w-[1089px]">
        <p className="text-9xl font-semibold">{encabezado_1}</p>
        <p className="text-9xl font-semibold self-center">{encabezado_2}</p>
        <p className="text-9xl font-semibold self-end">{encabezado_3}</p>
      </div>
      <p className="absolute text-xl top-16 right-24 max-w-[380px]">{descripcion}</p>

      <video className="mt-24 rounded-lg aspect-video w-[840px] self-center" controls>
        <source src={video} type="video/mp4" className="w-full aspect-video" />
      </video> 
      {/*
      <p>{descripcion}</p>
      <h1>{tituloMultimedia}</h1>
      */}
    </section>
  ) 
}
